p { margin: 0px; }

ul, ol, li { padding: 0px; margin: 0px; list-style: none; }

h1, h2, h3, h4, h5, .h1, .h2, .h3, .h4, .h5 { font-family: 'Montserrat', sans-serif; color: #000; text-transform: none; line-height: 1.42; font-weight: 700; }

h1, .h1 { font-size: 2.5rem; }

h2, .h2 { font-size: 2rem; }

h3, .h3 { font-size: 1.375rem; }

h4, .h4 { font-size: 1.125rem; }

h5, .h5 { font-size: 1rem; }

a { color: #000; text-decoration: none; outline: none; }

b, p, ul, li { padding: 0; margin: 0; }

img { max-width: 100%; height: auto; }

.header-top { height: 50px; background-color: #F3F2F2; }

@media (max-width: 1024px) { .header-top { display: none; } }

.header-top-links { display: flex; justify-content: space-between; padding: 15px 0 0 72px; }

@media (max-width: 1080px) { .header-top-links { padding: 15px 0; } }

.header-top-links a { margin-right: 60px; font-size: 18px; font-weight: 500; }

.header-top-links a:hover { opacity: .45; }

.header-middle { height: 93px; padding: 15px 0; }

@media (max-width: 1024px) { .header-middle { height: 65px; padding: 7px 0; box-shadow: 0px 2px 7px rgba(61, 61, 61, 0.25); } }

.header-middle .row { align-items: center; }

.header-middle-logo { display: flex; align-items: center; }

.header-middle-logo-subtitle { width: 167px; margin-left: 28px; font-size: 14px; font-weight: 300; }

@media (max-width: 1024px) { .header-middle-logo-subtitle { display: none; } }

.header-middle-call { display: flex; justify-content: flex-end; font-size: 22px; font-weight: 600; }

@media (max-width: 1024px) { .header-middle-call { display: none; } }

.header-middle-call-btn { background: #07BCA9; border: none; padding: 7px 16px; font-size: 15px; font-weight: 500; color: #fff; margin-right: 48px; cursor: pointer; }

.header-middle-call-btn:hover { opacity: .45; }

.header-middle-mob { display: none; }

@media (max-width: 1024px) { .header-middle-mob { display: flex; justify-content: flex-end; align-items: center; } }

.header-middle-mob-call { margin-right: 60px; }

.header-bottom { background-color: #0D19A3; }

@media (max-width: 1024px) { .header-bottom { display: none; } }

.header-bottom-catalog-link { width: 25%; text-align: center; padding: 16px 10px; color: #fff; font-size: 18px; font-weight: 600; }

.header-bottom-catalog .active { background-color: #FA3C1B; }

.header-mob-menu { display: none; width: 100%; }

.header-mob-menu-text { height: 40px; padding: 6px 0; background-color: #2F2FA2; color: #fff; font-size: 20px; font-weight: 500; border-bottom: 1px solid #fff; padding-left: 7px; }

.header-mob-menu-text.active { background-color: #FA3C1B; }

@media (max-width: 1024px) { .header-mob-menu.active { display: block; } }

.footer { width: 100%; padding: 20px 0; background-color: #F4F4F4; }

.footer-logo { margin-bottom: 10px; }

@media (max-width: 1024px) { .footer-logo img { width: 118px; } }

.footer-text { font-size: 14px; font-weight: 300; margin-bottom: 18px; }

@media (max-width: 1024px) { .footer-text { font-size: 10px; } }

.footer-title { font-size: 15px; font-weight: 600; margin-bottom: 5px; }

@media (max-width: 1024px) { .footer-title { font-size: 12px; } }

.footer-info { font-size: 12px; font-weight: 400; }

@media (max-width: 1024px) { .footer-info { font-size: 10px; }
  .footer-info br { display: none; } }

.footer-links { display: flex; flex-direction: column; margin-top: 20px; }

@media (max-width: 1024px) { .footer-links { display: none; } }

@media (max-width: 1024px) { .footer-links.catalog { display: flex; } }

.footer-links.catalog a { color: #0D19A3; }

@media (max-width: 1024px) { .footer-links.catalog a { margin-bottom: 10px; font-size: 12px; } }

.footer-links a { margin-bottom: 18px; font-size: 18px; font-weight: 500; text-decoration: underline; }

.footer-links a:hover { opacity: .35; }

* { margin: 0; padding: 0; box-sizing: border-box; }

html { font-size: 16px; height: 100%; min-height: 100%; -webkit-text-size-adjust: 100%; scroll-behavior: smooth; }

body { height: 100%; min-height: 100%; font-family: 'Montserrat', sans-serif; font-weight: 400; font-size: 1rem; color: #000; background-color: #fff; }

#wrapper { display: flex; flex-direction: column; margin-left: auto; margin-right: auto; position: relative; overflow: hidden; min-height: 100%; min-width: 100%; }

#middle { -webkit-box-flex: 1; flex: 1 0 auto; padding-bottom: 50px; }

#footer { -webkit-box-flex: 0; flex: 0 0 auto; }

.container { width: 100%; max-width: 1180px; margin: 0 auto; padding-left: 8px; padding-right: 8px; }
